/*DESKTOP VERSION */

@media only screen and (min-width: 1001px){

    .realizations-container{
        background-color: var(--Black);
        width:100%;
        height: auto;
        margin: 0;
        padding-top: 5rem;
        padding-left: 3rem;
        padding-right: 3rem;
        color:var(--White);
    }

    .realizations-container h2{
        display: block;
        margin: 2rem 0rem 4rem 0;
        padding-top:3rem;
        height: auto;
        font-size: 3rem;
        text-align: center;
    }
    .realizations{
       /* display: grid;
        align-items: center;
        width:80%;
        margin-left: 10%;
        height: auto;
        grid-template-columns: 1fr 1fr;
        justify-content: center;*/
        display: flex;
        flex-wrap: wrap;
        width: auto;
        margin: 3rem 0rem 4rem 0;
    }

    .realizations img{
        padding:1rem;
        width: 50%;
        margin: 0rem auto;

    }

}

/*MOBILE VERSION */

@media only screen and (max-width: 1000px){
    .realizations-container{
        background-color: var(--Black);
        width:100%;
        height: auto;
        margin: 3rem 0rem 4rem 0;
        color:var(--White);
    }
    
    .realizations-container h2{
        margin: 2rem 0rem 1rem 0;
        padding-top: 1rem;
        height: auto;
        width:100%;
        font-size: 1.2rem;
        text-align: center;
    }

    .realizations img{
        width: 85%;
        display: block;
        margin: 1rem auto;
    }

}