.picturemain{
    margin: 0 auto;
    display: block;
    position: relative;
}

img { 
    width: 100%;
    height: auto;
    border-bottom-right-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
}

/*DESKTOP VERSION */
@media only screen and (min-width: 851px){

.text1{
    position: absolute;
    width: 100%;
    top: 30vh;
    left: 50%; 
    transform: translate(-50%, -50%);
    color: var(--White);
    font-size: 7vw;
    font-weight: initial;
    text-align: center;
}

.text2{
    position: absolute;
    width: 100%;
    top: 55vh;
    left: 50%; 
    transform: translate(-50%, -50%);
    color: var(--White);
    font-size: 5vw;
    font-weight: 900;
    text-align: center;
}

.text3{
    position: absolute;
    width: 100%;
    top: 70vh;
    left: 50%; 
    transform: translate(-50%, -50%);
    color: var(--White);
    font-size: 4vw;
    font-weight: 500;
    text-align: center;
}
}

/*MOBILE VERSION */
@media only screen and (max-width: 850px){
    .text1{
        position: absolute;
        width: 100%;
        top: 22vh;
        left: 50%; 
        transform: translate(-50%, -50%);
        color: var(--White);
        font-size: 2.8rem;
        font-weight: initial;
        text-align: center;
        font-family: Montserrat;
    }
    .text2{
        position: absolute;
        width: 100%;
        top: 35vh; /* Increased from 29vh to 35vh */
        left: 50%; 
        transform: translate(-50%, -50%);
        color: var(--White);
        font-size: 2.5rem;
        font-weight: 900;
        text-align: center;
    }
    .text3{
        position: absolute;
        width: 100%;
        top: 48vh; /* Increased from 36vh to 48vh */
        left: 50%; 
        transform: translate(-50%, -50%);
        color: var(--White);
        font-size: 1.8rem;
        font-weight: 500;
        text-align: center;
    }
    img { 
        width: 120%;
        height: auto;
    }
}